@use "variables" as vars;

body {
    background-color: vars.$primary-dark;
}

.primary-bg-color {
    background-color: vars.$primary-color;
}

.dark-bg-color {
    background-color: vars.$primary-dark;
}

.secondary-bg-color {
    background-color: vars.$secondary-dark;
}

.text-primary-color {
    color: vars.$primary-color;
}

.container {
    max-width: 1320px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.primary-color-btn {
    font-size: 15px;
    font-weight: 600;
    line-height: 1.2;
    text-transform: capitalize;
    text-decoration: none;
    border: none;
    border-radius: 15px;
    padding: 16px 35px;
    background-color: vars.$primary-color;
    color: vars.$secondary-dark;
    transition: all 0.3s ease-in-out;
    display: inline-flex;
    align-items: center;
    gap: 6px;

    &:hover {
        background-color: vars.$secondary-dark;
        color: vars.$text-white;
    }

    svg {
      font-size: 12px;  
    }
}

.subtitle {
    font-size: 22px;
    font-weight: 500;
    line-height: 1.2;
    margin-bottom: 15px;
    color: vars.$text-gray;
}

.title {
    font-size: 45px;
    font-weight: 500;
    line-height: 1.2;
    color: vars.$text-white;

    @media screen and (max-width: 1024px) {
        font-size: 36px;
    }

    @media screen and (max-width: 1024px) {
        font-size: 28px;
    }
}

.content {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.85;
    color: vars.$text-gray;

    @media screen and (max-width: 767px) {
        line-height: 28px;
    }
}

.line-bg {
    position: relative;
    
    &:before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background: repeating-linear-gradient(to right, #222222, #37353500 1px, #0000000a 100px);
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }
}